// Helper classes, mixins, functions...

.uppercase {
  text-transform: uppercase; }

.light-gray {
  color: $light-gray; }

.medium-gray {
  color: $medium-gray; }

.dark-gray {
  color: $dark-gray; }

.blue {
  color: $blue; }

.dark-blue {
  color: $dark-blue; }

.orange {
  color: $orange; }

.cursive {
  font-style: italic; }

.text-center {
  text-align: center; }

.hide-mobile {
  @include media($tablet) {
    display: none; } }

.hide-desktop {
  @include media($medium-screen-up) {
    display: none; } }

.center-elements {
  margin: 0 auto; }



